body {
  background-color: #daa520;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Itim', cursive;
}

/* this is mean to just be the h1 selector but this is a temp fix */
#h1mainpage {
  font-family: 'Itim', cursive;
  font-size: 48px;
  color: whitesmoke
} 

header {
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.emptyDiv{
  width: 70px;
}

nav a {
  color: black;
  font-size: 24px;
  margin-left: 10px;
}

nav a:hover {
  opacity: 0.5;
  color: #449903;
}

#gamescreen {
  border: 5px solid black;
  /* transform: translate(-50%, -50%); */
  width: 800;
  height: 600;
}

#maintitle {
  width: 50%;
  margin: 20px;
}

.main-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  
}

